<template>
    <div class="alias">
        <div class="actions">
            <el-button @click="addItem" type="success" icon="el-icon-folder-add">添加分类</el-button>
        </div>
        <el-table class="full-width" :data="rows" v-loading="loading" stripe border row-key="id">
            <el-table-column
            v-for="(column, index) in columns"
            :key="index"
            :prop="column.name"
            :label="column.title"
            :width="column.width"
            :min-width="column.minWidth"
            sortable
            >
            </el-table-column>
            <el-table-column label="操作" min-width="120" width="150">
                <template slot-scope="scope">
                    <el-button size="mini" title="编辑" type="warning" @click="editItem(scope.row)" icon="el-icon-edit"></el-button>
                    <el-button size="mini" title="删除" type="danger" @click="removeItem(scope.row)" icon="el-icon-delete"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="paging padding">
            <el-pagination background layout="prev, pager, next" :total="aliasList.length" :current-page.sync="currentPage" :hide-on-single-page="true"/>
        </div>
        <el-dialog :title="isEdit ? '编辑分类' : '添加分类'" :visible.sync="addAliasDialog" width="450px" :modal-append-to-body="false" @closed="cancel">
            <el-form :model="alias" :rules="rules" ref="form" label-width="100px">
                <el-form-item prop="name" label="分类名称">
                    <el-input v-model="alias.name" placeholder="请输入分类名称"/>
                </el-form-item>
                <el-form-item prop="description" label="分类描述">
                    <el-input v-model="alias.description" placeholder="请输入分类描述"/>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button size="small" @click="addAliasDialog = false">取消</el-button>
                <el-button type="primary" size="small" @click="submit('form')">确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import * as http from '@/libs/request'
import { formatDate } from '@/libs/moment'
import { ObjectToFormdata } from '@/libs/formdata'

export default {
    data: () => ({
        columns: [
            {
                title: '分类名称',
                minWidth: 180,
                name: 'name',
            },
            {
                title: '分类描述',
                minWidth: 180,
                name: 'description'
            },
            {
                title: '创建时间',
                width: 180,
                name: 'created_at'
            },
            {
                title: '更新时间',
                width: 180,
                name: 'updated_at'
            },
        ],
        aliasList: [],
        currentPage: 1,
        isEdit: false,
        alias: {
            name: '',
            description: ''
        },
        rules: {
            name: [
                { required: true, message: '分类名称不能为空', trigger: 'blur' }
            ],
            description: [
                { required: true, message: '分类描述不能为空', trigger: 'blur' }
            ],
        },
        addAliasDialog: false,
        loading: false,
    }),
    methods: {
        test(row,expandedRows){
            console.log(row,expandedRows)
        },
        listAlias() {
            this.loading = true;
            http.listAlias({
                success: res => {
                    this.aliasList = res.resource_types ? res.resource_types.map(item => {
                        return {
                            id: item.id,
                            name: item.name,
                            description: item.desc,
                            created_at: formatDate(item.created_at),
                            updated_at: formatDate(item.updated_at)
                        }
                    }) : [];
                    this.loading = false;
                },
                fail: err => {
                    this.$message.error('获取资源分类失败, ' + err);
                    this.loading = false;
                }
            })
        },
        addItem() {
            this.isEdit = false;
            this.addAliasDialog = true;
        },
        editItem(row) {
            this.isEdit = true;
            this.alias = { ...row };
            this.addAliasDialog = true;
        },
        submit(form) {
            this.$refs[form].validate(valid => {
                if(!valid) {
                    return false;
                }
                else {
                    this.saveAlias()
                }
            })
        },
        saveAlias() {
            let { id, name, description } = this.alias;
            let formData = ObjectToFormdata({
                name: name,
                desc: description,
            });
            let callback = {
                success: res => {
                    this.reset();
                    this.addAliasDialog = false;
                    this.listAlias()
                },
                fail: err => {
                    this.$message.error('操作失败, ' + err)
                }
            };
            if(this.isEdit) {
                formData.append('id', id);
                http.updateAlias(formData, callback)
            }
            else {
                http.addAlias(formData, callback)
            }
        },
        cancel() {
            this.addAliasDialog = false;
            this.reset();
        },
        reset() {
            this.$refs.form.resetFields();
            this.alias = {
                name: '',
                description: ''
            }
        },
        removeItem(row) {
            this.$confirm('此操作将永久删除该分类, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.deleteAlias(row.id)
            })
        },
        deleteAlias(id) {
            http.deleteAlias({
                id
            }, {
                success: res => {
                    this.listAlias()
                },
                fail: err => {
                    this.$message.error('获取资源分类失败, ' + err)
                }
            })
        }
    },
    computed: {
        rows() {
            let limit = (this.currentPage - 1) * 10;
            return this.aliasList.slice(limit, limit + 10);
        }
    },
    mounted() {
        this.listAlias()
    },
}
</script>
